import React, { Component } from 'react';
import classNames           from 'classnames';

export default class extends Component {
    state = {
        isHidden: true,
        animated: false,
    };

    componentDidMount() {
        this.setState({
            isHidden: window.localStorage.getItem('hide-sidebar') === 'true',
        });

        setTimeout(() => {
            this.setState({ animated: true });
        }, 100);
    }

    toggleSidebar = () => {
        const { isHidden } = this.state;

        if (isHidden) {
            window.localStorage.setItem('hide-sidebar', 'false');
        } else {
            window.localStorage.setItem('hide-sidebar', 'true');
        }

        this.setState({ isHidden: !isHidden })
    };

    render() {
        const { isHidden, animated } = this.state;
        const { title, cover, subtitle } = this.props;

        return (
            <div className={ classNames('sidebar', { 'sidebar--hidden': isHidden, 'sidebar--animated': animated }) }
                 style={ { backgroundImage: `url('${cover}')` } }>
                <div className="sidebar__branding">
                    <h2>{ title }</h2>
                    <p>{ subtitle }</p>
                </div>
                <div className="sidebar__hide-area" onClick={ this.toggleSidebar } />

                <style jsx>{ `
.sidebar {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 35%;
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: sticky;
    top: 0;
}

.sidebar--monochrome {
    filter: grayscale(60%);
}

.sidebar__branding {
    font-size: 1.3em;
    line-height: 1.8em;
    color: #f4f4f4;
    text-shadow: -2px 2px 2px #232323;
    text-align: center;
    margin-bottom: 50%;
}

.sidebar__hide-area {
    width: 70px;
    height: 100%;
    align-self: flex-end;
    cursor: pointer;
    position: absolute;
    right: 0;
    background-color: rgba(0, 0, 0, .05);
    transition: all .2s ease-out;
}

.sidebar__hide-area:hover {
    background-color: rgba(0, 0, 0, .2);
}

.sidebar--hidden {
    margin-left: -32%;
    opacity: .05;
    transition: opacity .2s ease-out;
}

.sidebar--hidden:hover {
    opacity: 1;
}

.sidebar--animated {
    transition: all .5s ease-in-out;
}

.sidebar .sidebar__hide-area {
    cursor: w-resize;
}

.sidebar--hidden .sidebar__hide-area {
    cursor: e-resize;
}
        ` }</style>
            </div>
        )
    }
}
